.navbar {
    background: -webkit-gradient(linear, left top, right top, from(rgb(28, 27, 27)), to(rgb(26, 23, 23)));
    background: -webkit-linear-gradient(left, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    background: -o-linear-gradient(left, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    /* margin-left: 20px; */
    left: 10%;
    position: absolute;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }
  .navbar-logo:hover{
    color: rgb(100, 255, 218);

  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 90vw;
    -ms-flex-pack: end;
        justify-content: end;
    margin-right: 2rem;
  }

  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    /* border-bottom: 4px solid #fff; */
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    -webkit-box-shadow: 2px 2px 2px rgb(100, 255, 218);
            box-shadow: 2px 2px 2px rgb(100, 255, 218);
  }
  
  .fa-bars {
    color: #fff;
  }
  .fa-bars:hover{
    color: rgb(100, 255, 218);
  }
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
    
  }
  .resume-Btn{
    color: white;
    text-decoration: none;
  }
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      -webkit-transition: all 0.5s ease;
      -o-transition: all 0.5s ease;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      -webkit-transition: all 0.5s ease;
      -o-transition: all 0.5s ease;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
          -ms-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
    
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
    .fa-times:hover{
      color: rgb(100, 255, 218);
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      -webkit-transition: all 0.3s ease-out;
      -o-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      -webkit-transition: 250ms;
      -o-transition: 250ms;
      transition: 250ms;
    }
  }
  


